import infoBox from "../../components/boxstyle/infobox.vue"

export default {
	data() {
		return {
			carModeList: ['自翻卸车', '四轴车', '六轴车'],
			carList: ['9米以内', '9.6米', '11米', '13米', '其他'],
			// 上传长度
			upLength: [9, 9.6, 11, 12],
			// 当前选中状态
			carCurrent: 0,
			longCurrent: 0,
			// 表当绑定值
			carNum: '',
			companyName: '',
			length: null,
			// 上个页面的数据
			info: null
		}
	},

	methods: {
		clearCompanyName() {
			this.companyName = ''
		},

		// chooseCar(i) {
		// 	this.carCurrent = this.info.carType - 1;
		// },

		// chooseLong(i) {
		// 	this.longCurrent = this.length;
		// },

		// 初始化页面数据
		initPage() {
			this.carNum = this.info.carNo;
			this.companyName = this.info.companyName;
			this.carCurrent = this.info.carType - 1;
			// this.longCurrent = 4;
			this.length = this.info.containerLength;
			this.upLength.some((i, index) => {
				if (i === Number(this.info.containerLength)) {
					this.longCurrent = index;
					// this.length = '';
				}
			})
		},

		isLicensePlate(str) {
			return /^(([京津沪渝冀豫云辽黑湘皖鲁新苏浙赣鄂桂甘晋蒙陕吉闽贵粤青藏川宁琼使领][A-Z](([0-9]{5}[DF])|([DF]([A-HJ-NP-Z0-9])[0-9]{4})))|([京津沪渝冀豫云辽黑湘皖鲁新苏浙赣鄂桂甘晋蒙陕吉闽贵粤青藏川宁琼使领][A-Z][A-HJ-NP-Z0-9]{4}[A-HJ-NP-Z0-9挂学警港澳使领]))$/
				.test(str);
		},

		// 提交修改信息
		async modifyInfo() {
			uni.showLoading({});
			let isTure = this.isLicensePlate(this.carNum)
			if (isTure) {
				const opts = {
					url: '/sc/carMng/modCar',
					method: 'post'
				};

				let length = (this.longCurrent === 4 && Number(this.length)) || this.upLength[this.longCurrent]

				const params = {
					carId: Number(this.info.carId),
					carNo: this.carNum,
					carType: this.carCurrent + 1,
					companyName: this.companyName,
					containerLength: length
				};

				console.log(params)
				const res = await this.$http.httpTokenRequest(opts, params);
				console.log(res)

				if (res.data.code === 0) {
					uni.hideLoading({});
					uni.switchTab({
						url: "/pages/user/user"
					})
				} else {
					setTimeout(() => {
						uni.hideLoading({});
						uni.showModal({
							content: res.data.desc,
							showCancel: false
						})
					}, 700)
				}
			} else {
				setTimeout(() => {
					uni.hideLoading({});
					uni.showModal({
						content: "请输入正确车牌号"
					})
				}, 700)

			}

			if (this.longCurrent === 4 && Number(this.length) < 9) {
				uni.showModal({
					content: '请输入长度大于9的车辆信息',
					showCancel: false
				});
			} else {

				// if (res.data.code !== 0) {
				// 	uni.showModal({
				// 		content: "内容不能为空",
				// 		showCancel: false
				// 	})
				// }
				// 保证长度大于9
				// else {
				// uni.navigateTo({
				// 	url: "/pages/user/carower/managementcar"
				// })

				// }
			}

		}
	},

	onLoad(options) {
		this.info = options;
		this.initPage();
	},

	components: {
		infoBox
	}


}
